import 'regenerator-runtime/runtime'

import { FormEvent, useEffect, useState } from 'react'

import '../styles/tasklist.scss'

import { FiTrash, FiCheckSquare } from 'react-icons/fi'
import { api } from '../lib/api';

interface Task {
  id: number;
  title: string;
  isComplete?: boolean;
}

type TaskListProps = {
  title: string;
  isComplete?: boolean;
}

export function TaskList() {
  const [tasks, setTasks] = useState<Task[]>([]);
  const [newTaskTitle, setNewTaskTitle] = useState('');
  const [taskIsComplete, setTaskIsComplete] = useState<Boolean>()

  console.log(tasks)

  async function handleCreateNewTask() {
    // Crie uma nova task com um id random, não permita criar caso o título seja vazio.
    
    await api.post('/tasks',{
      title: newTaskTitle,
      isComplete: false
    })
  }

  async function handleToggleTaskCompletion(id: number, isComplete: boolean | undefined ) {
    // Altere entre `true` ou `false` o campo `isComplete` de uma task com dado ID 
   
    if(isComplete === false ){
      setTaskIsComplete(true)
    } else  setTaskIsComplete(false)

    await api.put(`/tasks/${id}`,{
       isComplete: taskIsComplete,
    })

}

  async function handleRemoveTask(id: number) {
    // Remova uma task da listagem pelo ID
    await api.delete(`/tasks/${id}`,{
    })
  }

    useEffect(() =>{
      api.get('https://6282fa4a38279cef71cf42c7.mockapi.io/api/v1/tasks')
      .then(response => response.data)
      .then(data => setTasks(data))
        
    },[])

  return (
    <section className="task-list container">
      <header>
        <h2>Minhas tasks</h2>

        <div className="input-group">
          <input 
            type="text" 
            placeholder="Adicionar novo todo" 
            onChange={(e) => setNewTaskTitle(e.target.value)}
            value={newTaskTitle}
          />
          <button type="submit" data-testid="add-task-button" onClick={handleCreateNewTask}>
            <FiCheckSquare size={16} color="#fff"/>
          </button>
        </div>
      </header>

      <main>
        <ul>
          {tasks.map(task => (
            <li key={task.id}>
              <div className={task.isComplete ? 'completed' : ''} data-testid="task" >
                <label className="checkbox-container">
                  <input 
                    type="checkbox"
                    readOnly
                    checked={task.isComplete}
                    onClick={() => handleToggleTaskCompletion(task.id, task.isComplete)}
                  />
                  <span className="checkmark"></span>
                </label>
                <p>{task.title}</p>
              </div>

              <button type="button" data-testid="remove-task-button" onClick={() => handleRemoveTask(task.id)}>
                <FiTrash size={16}/>
              </button>
            </li>
          ))}
          
        </ul>
      </main>
    </section>
  )
}